/**
 * @description 
 * @param {String} url
 * @return {Promise}
 * */

export function getGames(url) {
  var head = document.getElementsByTagName('head')[0];
  var script = document.createElement('script');
  return new Promise(function (resolve, reject) {
    script.src = url;
    script.type = 'text/javascript';

    script.onload = script.onreadystatechange = function () {
      if (!this.readyState || this.readyState === 'loaded' || this.readyState === 'complete') {
        var games = window.games;
        window.games = null;
        resolve(changeToObject(games));
      } else {
        reject(new Error(''));
      }

      head.removeChild(script);
    };

    head.appendChild(script);
  });
}
/**
 * @description 
 * @param  {Array} list gamesJs
 * @return {Array}      
 */

function changeToObject() {
  var list = arguments.length > 0 && arguments[0] !== undefined ? arguments[0] : [];
  var obj;
  list = list.map(function (item) {
    obj = {
      isFavorite: false,
      // 
      gameCode: platformCode[item[1]] // code

    };
    item.forEach(function (game, index) {
      obj[keys[index]] = game;
    });
    return obj;
  });
  return sort(list, 'popularity');
}
/**
 * @description 
 * @param  {Array}  list
 * @param  {String} sortField 
 * @param  {String} sortType   asc , desc 
 * @return {Array} list
 */


function sort(list, sortField) {
  var sortType = arguments.length > 2 && arguments[2] !== undefined ? arguments[2] : 'desc';
  list.sort(function (a, b) {
    a = sortField ? +a[sortField] : a;
    b = sortField ? +b[sortField] : b;
    return sortType === 'desc' ? b - a : a - b;
  });
  return list;
}
/**
 * @description 
 *
 * @param  {Array}  gameList 
 * @param  {Object} params   
 * @return {Object}          
 */


export function getPageData() {
  var gameList = arguments.length > 0 && arguments[0] !== undefined ? arguments[0] : [];
  var params = arguments.length > 1 ? arguments[1] : undefined;
  gameList = filterList(gameList, params);
  var page = params.page,
      pageSize = params.pageSize;
  var total = gameList.length;
  var data = gameList.slice(0, page * pageSize);
  var maxPage = Math.ceil(gameList.length / pageSize);
  return {
    page: page,
    pageSize: pageSize,
    data: data,
    total: total,
    maxPage: maxPage
  };
}
/**
 * @description 
 *
 * @param  {Array}  gameList 
 * @param  {Object} params   
 * @return {Array}
 */

function filterList(gameList, params) {
  var _loop = function _loop(key) {
    if (params.hasOwnProperty(key) && params[key] === true) {
      gameList = gameList.filter(function (item) {
        return item[key];
      });
    }
  };

  // 
  for (var key in params) {
    _loop(key);
  } // 


  if (params.search) {
    var reg = new RegExp(params.search);
    gameList = gameList.filter(function (item) {
      return reg.test(item.cnName) || reg.test(item.enName);
    });
  } // 


  if (params.providerList && params.providerList.length) {
    gameList = gameList.filter(function (item) {
      return ~params.providerList.indexOf(item.provider);
    });
  } // 


  if (params.lineList && params.lineList.length) {
    var temp = [];
    params.lineList.forEach(function (line) {
      line = line.split('-');
      gameList.forEach(function (game) {
        if (game.line >= line[0] && game.line <= line[1]) {
          temp.push(game);
        }
      });
    });
    gameList = temp;
  }

  return gameList;
}
/**
 * @description 
 *
 * @param {Object} param    
 *                 gameId   id
 *                 gameCode code
 *                 gameType 
 *                 gameName 
 *                 tryPlay  
 */


export function playGame() {
  var param = arguments.length > 0 && arguments[0] !== undefined ? arguments[0] : {};
  var gameCode = param.gameCode,
      canTryPlay = param.canTryPlay;
  var loginStatus = store.getters.loginStatus;
  param.canTryPlay = canTryPlay || tryPlayCode[gameCode] || tryPlayCode[platformCode[gameCode]];

  if (loginStatus) {
    getGameUrl(param);
  } else if (param.canTryPlay) {
    getGameUrl(param);
  } else {
    toast.error({
      duration: 1500,
      message: '',
      callback: function callback() {
        router.push('/login').then(function (r) {
          return r;
        });
      }
    });
  }
}
/**
 * @description 
 *
 * @param {Object}  param      
 */

function getGameUrl(param) {
  var routeData = router.resolve({
    path: '/loadingGame',
    query: param
  });
  window.open(routeData.href, '_blank', 'height=667, width=375, scrollbars=yes,resizable=1,modal=false,alwaysRaised=yes');
}

